import { ModuleS, NoReply } from "odin";
import { RoleData } from "./RoleData";
import { RoleModuleC } from "./RoleModuleC";

export class RoleModuleS extends ModuleS<RoleModuleC, RoleData>{

    public override onStart(): void {
        super.onStart();
    }
    onPlayerEnterGame(player: Gameplay.Player, reenter: boolean): void {
        super.onPlayerEnterGame(player, reenter);
        // console.log('enter ', player.getPlayerID())
        this.getAllClient().net_hideRole(player.getPlayerID());
    }

    /**
     * 保存衣服数据
     * @param id 
     */
    @NoReply
    public net_saveClothesData(id: number) {
        console.log(`cundang ${id}`);
        this.currentData.saveClothesData(id);
    }

    /**
     * 更新当前装备的服装
     * @param id 
     */
    @NoReply
    public net_saveEquipData(id: number) {
        this.currentData.saveEquipID(id);
    }
}