import { ModuleManager, Singleton } from "odin";
import { GameConfig } from "../config/GameConfig";
import { SPSound } from "../const/Define";
import { RoleModuleC } from "../modules/role/RoleModuleC";

/** 
 * @Author       : pengwei.shi
 * @Date         : 2022-11-27 10:19:48
 * @LastEditors  : pengwei.shi
 * @LastEditTime : 2022-12-02 14:41:13
 * @FilePath     : \streetparkour\JavaScripts\untils\Sound.ts
 * @Description  : 修改描述
 */
@Singleton()
export class Sound {
    public static instance: Sound;

    /**
     * 播放bgm
     * @param soundType 
     */
    public gameBGM(soundType: SPSound) {
        let voice = this.getAsset(soundType);
        let role = ModuleManager.instance.getModule(RoleModuleC);
        let guid = voice.guid[role.gender - 1] || voice.guid[0];
        this.playBGM(guid, voice.SoundPropportion);
        // console.log(`播放的音效是：${soundType}`)
    }

    /**
     * 播放音效
     * @param soundType 
     */
    public gameSound(soundType: SPSound) {
        let voice = this.getAsset(soundType);
        let role = ModuleManager.instance.getModule(RoleModuleC);
        let guid = voice.guid[role.gender - 1] || voice.guid[0];
        switch (soundType) {
            case SPSound.Coin:
                this.playSound(guid, true, voice.SoundPropportion, voice.loop);
                break;
            default:
                this.playSound(guid, true, voice.SoundPropportion, voice.loop);
                break;
        }
    }

    /**
     * 设置音效是否生效
     * @returns 当前音效是否生效
     */
    public setSoundActive(): boolean {
        let vscale = Service.SoundService.getInstance().BGMVolumeScale;
        if (vscale == 0) {
            Service.SoundService.getInstance().BGMVolumeScale = 1;
            Service.SoundService.getInstance().volumeScale = 1;
            return true;
        } else {
            Service.SoundService.getInstance().BGMVolumeScale = 0;
            Service.SoundService.getInstance().volumeScale = 0;
            return false;
        }
    }

    /**
     * 获取音效资源
     * @param index 
     * @returns 
     */
    private getAsset(index: number) {
        return GameConfig.Voice.getElement(index);
    }

    /**
    * 播放bgm
    * @param guid 
    * @param volume 
    */
    private playBGM(guid: string, volume?: number) {
        Service.SoundService.getInstance().stopBGM();
        Service.SoundService.getInstance().playBGM(guid, volume);
    }

    /**
     * 
     * @param guid 
     * @param volume 
     * @param needPauseSound 是否需要暂停音效
     */
    private playSound(guid: string, needPauseSound: boolean = true, volume?: number, loopNum?: number) {
        if (needPauseSound) {
            Service.SoundService.getInstance().stopAllSound();
        }
        Service.SoundService.getInstance().playSound(guid, loopNum, volume);
    }
}