import { DataInfo, ModuleData } from "odin";
import { GuideTarget } from "../../const/Define";


export class GameInfo extends DataInfo {

    /**
     * 货币数
     */
    public coinNum: number = 0;
    //宝石数
    public gemNum: number = 0;
    /**
     * 最高分
     */
    public score: number = 0;
    //新手关卡引导
    public guideScene: number = 0;
    //商店引导
    public guideShop: number = 0;
    //角色引导
    public guideRole: number = 0;


}

export class GameData extends ModuleData<GameInfo> {

    protected onDataInit(): void {
        if (!this.dataInfo.guideScene)
            this.dataInfo.guideScene = 0;
        if (!this.dataInfo.gemNum)
            this.dataInfo.gemNum = 0;
        if (!this.dataInfo.guideShop)
            this.dataInfo.guideShop = 0;
        if (!this.dataInfo.guideRole)
            this.dataInfo.guideRole = 0;
    }
    protected initDefaultData(): void {
        super.initDefaultData();
        this.dataInfo.coinNum = 0;
        this.dataInfo.score = 0;
        this.dataInfo.guideScene = 0;
        this.dataInfo.gemNum = 0;
        this.dataInfo.guideRole = 0;
        this.dataInfo.guideShop = 0;
        //console.log(this.dataInfo.score)
    }

    /**
     * 获取引导
     * @param target 目标
     * @returns 
     */
    public getGuide(target: GuideTarget): number {
        let re: number = 0;
        switch (target) {
            case GuideTarget.Scene:
                re = this.dataInfo.guideScene;
                break;
            case GuideTarget.Role:
                re = this.dataInfo.guideRole;
                break;
            case GuideTarget.Shop:
                re = this.dataInfo.guideShop;
                break;
            default:
                break;
        }
        return re;
    }


    /**
     * 设置新手引导是否完成
     * @param target 
     * @param guide 
     */
    public setGuide(target: GuideTarget, guide: number) {
        switch (target) {
            case GuideTarget.Scene:
                this.dataInfo.guideScene = guide;
                break;
            case GuideTarget.Role:
                this.dataInfo.guideRole = guide;
                break;
            case GuideTarget.Shop:
                this.dataInfo.guideShop = guide;
                break;
            default:
                break;
        }
        this.saveData(true);
    }

    /**
     * 增加货币
     * @param coin 金币数量
     * @param gem 宝石数量
     */
    public addMoney(coin: number, gem: number) {
        this.dataInfo.coinNum += coin;
        this.dataInfo.gemNum += gem;
        this.saveData(true);
    }
    /**
     * 消耗货币
     * @param coin 金币数量
     * @param gem 宝石数量
     * @returns  是否成功
     */
    public costMoney(coin: number, gem: number): boolean {
        if (!this.enough(coin, gem))
            return false;
        this.dataInfo.coinNum -= coin;
        this.dataInfo.gemNum -= gem;
        this.saveData(true);
        return true;
    }
    /**
     * 消耗货币
     * @param coin 金币数量
     * @param gem 宝石数量
     * @returns  是否成功
     */
    public enough(coin: number, gem: number): boolean {
        if (this.dataInfo.coinNum < coin)
            return false
        if (this.dataInfo.gemNum < gem)
            return false;
        return true;
    }
    /**
     * 获取当前货币
     */
    public get curCoin(): number {
        return this.dataInfo.coinNum;
    }
    /**
     * 获取当前货币
     */
    public get curGem(): number {
        return this.dataInfo.gemNum;
    }

    // /**
    //  * 直接设置货币数量
    //  * @param num 数量
    //  */
    // public setCoin(num: number) {
    //     this.dataInfo.coinNum = num;
    //     this.saveData(true);
    // }
    /**
     * 设置得分
     * @param score 得分
     */
    public setScore(score) {
        this.dataInfo.score = score;
        this.saveData(true);
    }
    /**
     * 获取最高分
     */
    public get maxScore(): number {
        return this.dataInfo.score;
    }
}