import { DataCenterC, DataInfo, ModuleData } from "odin";

/** 新手引导 存档数据 结构 */
export class GuideSrcData extends DataInfo {

    /**
     * 已经完成的引导
     */
    public complateGuide: number[] = null;

    /**
     * 当前引导
     */
    public curGuide: number = 0;

}

/** 新手引导 数据 操作类 */
export class GuideDataHelper extends ModuleData<GuideSrcData> {

    public onGuideChangeAction: Type.Action = new Type.Action();

    public constructor() {
        super(GuideSrcData);
    }

    /**
     * 初始化引导
     */
    protected initDefaultData(): void {
        if (this.dataInfo.complateGuide == null)
            this.dataInfo.complateGuide = [];
    }

    /**
     * 重置所有引导存档
     */
    public resetAllGuide() {
        this.dataInfo.complateGuide = [];
        this.dataInfo.curGuide = 0;
        this.saveData(true);
    }

    /**
     * 重置引导存档
     * @param guideId 
     */
    public resetGuideById(guideId: number): boolean {

        let newList = [];
        let has = false;
        this.dataInfo.complateGuide.forEach(e => {
            if (e == guideId) {
                has = true;
                return;
            }
            newList.push(guideId);
        })
        if (!has) return true;
        this.dataInfo.complateGuide = newList;
        if (this.dataInfo.curGuide == guideId) {
            this.dataInfo.curGuide = 0;
        }
        this.saveData(true);
        return true;

    }

    /**
     * 完成引导
     * @param guideId 引导id
     */
    public complateGuide(guideId: number) {
        this.dataInfo.complateGuide.push(guideId);
        this.onGuideChangeAction.call();
        this.saveData(true);

    }

    /**
     * 引导是否完成
     * @param guideId 引导id
     * @returns 
     */
    public guideIsComplate(guideId: number): boolean {
        let guide = this.dataInfo.complateGuide.findIndex(e => { return e == guideId; })
        if (guide >= 0) {
            return true;
        }
    }

    /**
     * 获取当前引导
     */
    public getCurGuide(): number {
        return this.dataInfo.curGuide;
    }

    /**
     * 设置当前引导阶段
     * @param val 
     */
    public setCurGuide(val: number) {
        this.dataInfo.curGuide = val;
        this.saveData(true);
    }

}