import { GameConfig } from "../../config/GameConfig";
import { ResourceType } from "../../const/Define";
import { DataManager } from "../../data/DataManager";
import { MGSCenter } from "../../untils/MGSCenter";

/*
 * @Author: pengwei.shi
 * @Date: 2022-11-07 17:26:34
 * @LastEditors: pengwei.shi
 * @LastEditTime: 2022-11-27 15:50:46
 * @FilePath: \streetparkour\JavaScripts\modules\obstacle\Obstacle.ts
 * @Description: 障碍
 */
export class Obstacle {
    public id: number
    private _obj: Core.GameObject;
    /**资源类型 */
    public resourceType: ResourceType;
    private _defaultPos: Type.Vector;
    //金币objs
    private _coinList: Core.GameObject[];
    //道具objs
    private _itemList: Core.GameObject[];
    //通过标记objs
    private _passList: Core.GameObject[];
    /**障碍位置 */
    private _obloction: number;
    /**障碍长度 */
    private _obLenght: number;
    private _autoList: Core.GameObject[];
    private _autoDis: number;
    /**障碍是否生效 */
    private _isActive: boolean;
    /**是否是道具 */
    public isItem: boolean;
    private _isReady: boolean;

    public constructor(id: number) {
        this.id = id;
        this._isReady = false;
    }
    public async init(): Promise<any> {
        let cfg = GameConfig.Assets.getAllElement().find(i => i.NameID == this.id);
        this.isItem = cfg.IsItem ? true : false;
        this._defaultPos = new Type.Vector(0, 0, -5000);
        this.resourceType = cfg.ResourceGroup;
        this._coinList = [];
        this._autoList = [];
        this._itemList = [];
        this._passList = [];
        this._obloction = 0;
        this._autoDis = 0;
        this._obLenght = cfg.Lenght ? cfg.Lenght : 0;
        this._isActive = false;
        this._obj = await Core.GameObject.asyncSpawnGameObject(cfg.PrefabsGuid);
        return this._obj.ready().then(readyObj => {
            let childs = readyObj.getChildren();
            //this._obj.setCollision(Type.CollisionStatus.QueryOnly,true);
            for (let child of childs) {
                let str = child.name;
                if (str === 'coins') {
                    let list = child.getChildren();
                    for (let c of list) {
                        this._coinList.push(c);
                    }
                }
                else if (str === 'items') {
                    let list = child.getChildren();
                    for (let c of list) {
                        this._coinList.push(c);
                    }
                }
                else if (str === 'pass') {
                    this._passList.push(child);
                }
                else if (str === 'auto') {
                    this._autoList.push(child);
                }
                else if (str.indexOf('block') >= 0 || str.indexOf('board') >= 0) {
                    MGSCenter.addGuid(child.guid, this.id);
                    //child.setCollision(Type.CollisionStatus.QueryOnly);
                }
            }
            this._isReady = true;
        });
    }
    public setLocation(loc: Type.Vector) {
        if (!this._isReady) return;
        this._obloction = loc.x;
        this._obj.worldLocation = loc;
        this._isActive = true;
    }
    /**
     * 隐藏
     */
    public setHide(dis: number) {
        if (this._obloction - DataManager.gameDis < dis)
            this.resetPos();
    }
    public onUpdate(dt: number) {
        // if (this._coinList.length > 0) {
        //     for (let obj of this._coinList) {
        //         let rot = obj.rotation;
        //         rot.z -= dt * 150;
        //         obj.rotation = rot;
        //     }
        // }
        //移动物件
        if (this._autoList.length > 0) {
            let ds = this._obloction - DataManager.gameDis
            if (ds > 0 && ds < 3000) {
                let dis = DataManager.carSpeed * dt;
                for (let obj of this._autoList) {
                    let pos = obj.worldLocation;
                    pos.x -= dis;
                    obj.worldLocation = pos;
                }
                this._autoDis += dis;
            }
        }
        //判断障碍物是否生效
        let obstacleLen = this._obloction + this._obLenght;
        //1200 为玩家距离场景x轴原点的位置
        if (DataManager.gameDis - 1200 > obstacleLen) {
            this._isActive = false;
        }
    }
    /**
     * 重置位置
     * 供对象池调用
     */
    public resetPos() {
        if (!this._isReady) return;
        this._obj.worldLocation = this._defaultPos;
        this._isActive = false;
        if (this._coinList.length > 0) {
            this._coinList.forEach((obj) => {
                obj.setVisibility(Type.PropertyStatus.On, true);
                obj.setCollision(Type.CollisionStatus.QueryOnly)
            });
        }
        if (this._itemList.length > 0) {
            this._itemList.forEach((obj) => {
                obj.setVisibility(Type.PropertyStatus.On, true);
                obj.setCollision(Type.CollisionStatus.QueryOnly)
            });
        }
        for (let obj of this._autoList) {
            let pos = obj.worldLocation;
            pos.x += this._autoDis;
            obj.worldLocation = pos;
        }
        for (let obj of this._passList) {
            obj.setVisibility(Type.PropertyStatus.On, true);
            obj.setCollision(Type.CollisionStatus.QueryOnly)
        }
    }

    /**获取障碍长度 */
    public get getObLenght() {
        return this._obLenght
    }

    /**获取障碍物是否生效 */
    public get active() {
        return this._isActive;
    }
}