import { DataInfo, ModuleData } from "odin";

class RoleInfo extends DataInfo {
    /**已获得的服装id */
    public clothesIDs: number[] = [];
    /**装备的服装ID */
    public equipID: number = 1;
}

export class RoleData extends ModuleData<RoleInfo>{

    protected onDataInit(): void {
        if (!this.dataInfo.clothesIDs || !this.dataInfo.equipID) {
            this.dataInfo.equipID = 1;
            this.dataInfo.clothesIDs = [1];
        }
    }

    protected initDefaultData(): void {
        super.initDefaultData();
        this.dataInfo.clothesIDs = [1];
        this.dataInfo.equipID = 1;
    }

    public get curClothesIDs() {
        return this.dataInfo.clothesIDs;
    }

    public get curEquitID() {
        return this.dataInfo.equipID;
    }
    public setCurEquitID(val: number) {
        this.dataInfo.equipID = val;
    }


    public saveClothesData(clothesID: number) {
        this.dataInfo.clothesIDs.push(clothesID);
        this.saveData(true);
    }

    public saveEquipID(clothesID: number) {
        this.dataInfo.equipID = clothesID;
        this.saveData(true);
    }

}