@Core.Class
export default class GameStart extends OdinGame {

    @Core.Property({ displayName: "是否线上存储" })
    public isOnlineSave: boolean = true;

    public onStart(): void {
        super.onStart();
        if (Util.SystemUtil.isClient()) {
            //设置配置表语言
            GameConfig.initLanguage(Utils.getLanguage(), (key) => {
                let ele = GameConfig.Language.getElement(key);
                if (ele == null) return "unknow_" + key;
                return ele.Value;
            });
            UI.UIBehavior.addBehavior('lan', (ui: UI.StaleButton | UI.TextBlock) => {
                let key: string = ui.text;
                if (key) {
                    let lan = GameConfig.Language.getElement(key);
                    if (lan) {
                        ui.text = (lan.Value);
                    }
                }
            })
            UI.UIManager.instance.show(LoadingUI);
        }
        else if (this.isOnlineSave) {
            //console.log(Util.SystemUtil.isPIE, Util.SystemUtil.isMobile());
            DataStorage.setTemporaryStorage(Util.SystemUtil.isPIE)
        }

        // DataStorage.setEvn(Global.isEditor);

    }
    protected onRegisterModule(): void {
        //
        ModuleManager.instance.register(GameModuleS, GameModuleC, GameData);
        //ModuleManager.instance.setClientFirstStartModule(GameModuleC);
        ModuleManager.instance.register(ItemModuleS, ItemModuleC, ItemData);
        ModuleManager.instance.register(RoleModuleS, RoleModuleC, RoleData);
        ModuleManager.instance.register(SceneModuleS, SceneModuleC, null);
        ModuleManager.instance.register(ObstacleModuleS, ObstacleModuleC, null);
        ModuleManager.instance.register(GuideModuleS, GuideModuleC, GuideDataHelper);

    }
    onUpdate(dt: number): void {
        super.onUpdate(dt);
        Util.TweenUtil.TWEEN.update();
    }
    protected onClientLoading(msg: string, progress: number, completeAotoClose: boolean): void {
        // if (!this._loadingUI) {
        //     this._loadingUI = UI.UIManager.instance.create(LoadingUI);
        //     this._loadingUI.rootCanvas.slot.zOrder = 2200;
        //     this._loadingUI.setVisible(UI.SlateVisibility.Visible);
        // }
        // this._loadingUI.setProgress(progress);
        // console.log(progress)
        // if (progress >= 1) {
        //     this._loadingUI.setVisible(UI.SlateVisibility.Hidden);
        //     this.enterGame();
        // }
    }
}

